.swipeButtons {
  position: fixed;
  bottom: 8vh;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.swipeButtons .MuiIconButton-root {
  background-color: white;
  box-shadow: 0px 10px 50px 0 rgba(0, 0, 0, 0.3);
}

.swipeButtons__repeat {
  padding: 2vh;
  color: #f5b748;
}

.swipeButtons__left {
  padding: 2vh;
  color: #ec5e6f;
}

.swipeButtons__star {
  padding: 2vh;
  color: #62b4f9;
}

.swipeButtons__right {
  padding: 2vh;
  color: #76e2b3;
}

.swipeButtons__lightning {
  padding: 2vh;
  color: #915dd1;
}
